.loader {
  width: 50px;
  aspect-ratio: 1;
  border-radius: 50%;
  background:
    radial-gradient(farthest-side,#2e86de 94%,#0000) top/8px 8px no-repeat,
    conic-gradient(#0000 30%,#2e86de);
  -webkit-mask: radial-gradient(farthest-side,#0000 calc(100% - 8px),#000 0);
  animation: l13 1s infinite linear;
}
@keyframes l13{
  100%{transform: rotate(1turn)}
}

@keyframes all-to-coupon-code-border-keyframes {
  from {
    left: 0;
    top: 0;
    width: '100%';
    height: '100%';
    filter: '';
    background-color: '#00000000';
    border: '0 solid #ffffff7f';
  }
  to {
    left: 15;
    top: 55;
    width: 120;
    height: 32;
    filter: 'blur(8px)';
    background-color: '#0000002f';
    border: '2px solid #ffffffaf';
  }
}

.all-to-coupon-code-border {
  animation-name: all-to-coupon-code-border-keyframes;
  animation-duration: 0.3s;
}

@keyframes all-to-coupon-code-keyframes {
  from {
    filter: '';
    background-color: '';
  }
  to {
    filter: 'blur(8px)';
    background-color: '#0000002f';
  }
}

.all-to-coupon-code {
  animation-name: all-to-coupon-code-keyframes;
  animation-duration: 0.3s;
}

@keyframes coupon-code-to-phone-border-keyframes {
  from {
    left: 0;
    top: 0;
    width: '100%';
    height: '100%';
    filter: '';
    background-color: '#00000000';
    border: '0 solid #ffffff7f';
  }
  to {
    left: 15;
    top: 100;
    width: 180;
    height: 40;
    filter: 'blur(8px)';
    background-color: '#0000002f';
    border: '2px solid #ffffffaf';
  }
}

.coupon-code-to-phone-border {
  animation-name: coupon-code-to-phone-border-keyframes;
  animation-duration: 0.3s;
}

@keyframes clear-box-keyframes {
  from {
    left: 15;
    top: 100;
    width: 180;
    height: 40;
    filter: 'blur(8px)';
    background-color: '#0000002f';
    border: '2px solid #ffffffaf';
  }
  to {
    left: 0;
    top: 0;
    width: '100%';
    height: '100%';
    filter: '';
    background-color: '#00000000';
    border: '0 solid #ffffff7f';
  }
}

.clear-box {
  animation-name: clear-box-keyframes;
  animation-duration: 0.3s;
}
