.myBox1 {
  background-color: #00000000;
  font-size: 15px;
  padding-left: 10px;
  padding-right: 10px;
  border: 2px solid #0e7bf1;
  outline: none;
  animation-name: myBox1Anim2;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

.myBox1:hover {
  animation-name: myBox1Anim1;
  animation-duration: 0.2s;
  animation-fill-mode: forwards;
}

@keyframes myBox1Anim1 {
  from {
    border: 2px solid #0e7bf1;
  }
  to {
    border: 2px solid #FAC03B;
  }
}

@keyframes myBox1Anim2 {
  from {
    border: 2px solid #FAC03B;
  }
  to {
    border: 2px solid #0e7bf1;
  }
}

.myBox1:focus {
  border: 2px solid #FAC03B;
}

.myBox1:active {
  border: 2px solid #FAC03B;
}
